import React from 'react';
import {
	HashRouter,
	NavLink
}from 'react-router-dom';

import './bio.css';

class Link extends React.Component {
	render() {
		return (
			<NavLink className="link" to={"/" + this.props.name} >
				<img src={this.props.img} alt={this.props.alt} />
				<h3>{this.props.name}</h3>
			</NavLink>
		)
	}
}

export default class Choice extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			members: [
				{name: "Callum Minks", img: 'https://indigobay.uk/resources/images/the depot/callum-smile.jpg', alt: "Callum Minks Smiling Whilst Performing at The Depot"},
				{name: "Alex Plater", img: 'https://indigobay.uk/resources/images/yardbirds/yardbirds-alex.jpg', alt: "Alex Plater Performing At The Lincoln Imp"},
				{name: "George Gray", img: 'https://indigobay.uk/resources/images/the depot/george-2.jpg', alt: "George Gray Tuning His Bass Guitar At The Yardbirds"},
				{name: "Amias Burrows", img: "https://indigobay.uk/resources/images/the depot/amias-performance.jpg", alt: "Amias Burrows Performing At The Depot"}
			]
		}
	}

	render() {
		return (
			<section>
				<HashRouter>
					{this.state.members.map((member, i) => (
						<Link key={i} name={member.name} img={member.img} alt={member.alt} />
					))}
				</HashRouter>
			</section>
		)
	}
}
