import React from 'react';
import ReactDOM from 'react-dom';
import {
	HashRouter,
	Route
} from 'react-router-dom';

import Default from './bio';
import Callum from './Callum';
import Alex from './Alex';
import George from './George';
import Amias from './Amias';

ReactDOM.render(
	<HashRouter>
		<div>
			<Route exact strict path="/" component={ Default }/>
			<Route path="/Callum Minks" component={ Callum }/>
			<Route path="/Alex Plater" component={ Alex }/>
			<Route path="/George Gray" component={ George }/>
			<Route path="/Amias Burrows" component={ Amias }/>
		</div>
	</HashRouter>,
  document.getElementsByTagName('main')[0]
);
