export default function age(dob, sign) {
	var day = "";
	var month = "";
	var year = "";
	
	var date = new Date();
	var currentDay = date.getDate();
	var currentMonth = date.getMonth() + 1;
	var currentYear = date.getFullYear();
	
	var part = 0;
	for (var i = 0; i < dob.length; i++) {
		switch (part) {
			case 0:
				if (dob[i] == " ") {
					part++;
				} else {
					day += dob[i];
				}
				break;
			case 1:
				if (dob[i] == " ") {
					part++;
					switch (month) {
						case "January":
						case "Jan":
							month = 1;
							break;
						case "February":
						case "Feb":
							month = 2;
							break;
						case "March":
						case "Mar":
							month = 3;
							break;
						case "April":
						case "Apr":
							month = 4;
							break;
						case "May":
							month = 5;
							break;
						case "June":
						case "Jun":
							month = 6;
							break;
						case "July":
						case "Jul":
							month = 7;
							break;
						case "August":
						case "Aug":
							month = 8;
							break;
						case "September":
						case "Sept":
							month = 9;
							break;
						case "October":
						case "Oct":
							month = 10;
							break;
						case "November":
						case "Nov":
							month = 11;
							break;
						case "December":
						case "Dec":
							month = 12;
							break;
					}
				} else {
					month += dob[i];
				}
				break;
			case 2:
				year += dob[i];
				break;
		}
	}
	
	var age = currentYear - year;
	if (month > currentMonth) {
		age--;
	} else if (month == currentMonth && currentDay < day) {
		age--;
	}
	return dob + " (" + age + ") (" + sign + ")";
}
